/* ---------------------------
  02.Utility -> { Positioning } 
------------------------------*/
.absolute-top-left {
  position: absolute;
  top: 0;
  left: 0;
}

.absolute-top-right {
  position: absolute;
  top: 0;
  right: 0;
}

.absolute-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.absolute-center-right {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.absolute-center-left {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.absolute-bottom-left {
  position: absolute;
  bottom: 0;
  left: 0;
}

.absolute-bottom-right {
  position: absolute;
  bottom: 0;
  right: 0;
}

@media (min-width: 480px) {
  .position-sm-absolute {
    position: absolute !important;
  }
}

@media (min-width: 992px) {
  .position-lg-absolute {
    position: absolute !important;
  }
}

/* Translate Classes*/
.translate-n50 {
  transform: translate(-50%, -50%);
}

.translateX-n50 {
  transform: translateX(-50%);
}

.translateY-50 {
  transform: translateY(50%);
}
